import {Component, ElementRef, Input, AfterContentInit, AfterViewInit} from "@angular/core";
import {ROUTER_DIRECTIVES} from "@angular/router-deprecated";

import {SidebarSubmenu} from "carbon-panel/sidebar.service";

import template from "./sidebar-submenu.component.html!";

@Component( {
	selector: "a.item.submenu",
	template: template,
	directives: [ ROUTER_DIRECTIVES, SidebarItemsComponent ]
} )
export class SidebarItemsComponent implements AfterContentInit, AfterViewInit {
	@Input( "item" ) item:SidebarSubmenu;

	private $element:JQuery;

	constructor( private element:ElementRef ) {}

	ngAfterContentInit():void {
		this.$element = $( this.element.nativeElement );
	}

	ngAfterViewInit():void {
		this.$element.accordion( {
			trigger: ".item.submenu, .item.submenu .title",
			title: ".title"
		} );
	}
}

export default SidebarItemsComponent;
